.productCard {
  height: 372px;
  width: 278px;
  background-color: #FFF;
}

.imgWrapper {
  height: 224px;
  width: 278px;
}

.wrapper {
  padding: 25px;
  padding-top: 15px;
  height: 148px;
  display: grid;
  grid-template-columns: auto 50px;
}

.textWrapperLeft {
  font-family: 'Mulish', sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;

  margin-right: auto;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.name {
  margin-bottom: 10px;
}

.textWrapperRight {
  font-family: 'Mulish', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  text-align: end;
}

.accentBC {
  background-color: #F4F4F4;
}