.feed {
  background-color: #C72230;
  padding: 40px 0px;
}

.wrapper {
  width: 1550px;
  margin: auto;
}

.feed h2 {
  font-family: 'Mulish', sans-serif;
  font-size: 24px;
  font-weight: 800;
  line-height: 1.25;
  color: #FFFFFF;
  margin-bottom: 40px;
}

.productCardsWrapper {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 40px;
}

.sceletonWrapper {
  background-color: #FFFFFF;
}

.moreIcon {
  margin-left: 20px;
}